import React from 'react'
import { Link } from 'react-router'
import { observable, computed } from 'mobx'
import { observer } from "mobx-react"

import DirectSalePriceCarType from '../components/direct_sale_price/direct_sale_price_car_type'
import DirectSalePricePurchaseFilter from '../components/direct_sale_price/direct_sale_price_purchase_filter'

@observer
class DirectSalePrice extends React.Component{
	constructor(props){
		super(props)
	}

	render(){
		return(
			<div className="direct_sale_price">
				<DirectSalePriceCarType/>
				<DirectSalePricePurchaseFilter/>
			</div>
		)
	}
}

export default DirectSalePrice



