import React from 'react'

import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import DirectSaleDetailTabCarTypeContent from '../direct_sale_detail/direct_sale_detail_tab_cartype_content'

class DirectSaleTabCarInfo extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
  	return(
  		<div className="car_info">
  			<DirectSaleDetailTabCarTypeContent/>
  		</div>
  	)
  }
}

class DirectSaleTabConf extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
  	return(
  		<div className="configuration">
  			<div className="carName">
  				<p>3 2016  328Li xDrive 2.0T </p>
  			</div>
  			<ul>
  				<li>
  					<p></p>
  					<p><span></span><span></span>-</p>
  					<ul>
  						<li>
  							<span></span>
  							<span>11.38</span>
  						</li>
  						<li>
  							<span></span>
  							<span>9.88</span>
  						</li>
  						<li>
  							<span></span>
  							<span></span>
  						</li>
  						<li>
  							<span></span>
  							<span></span>
  						</li>
  						<li>
  							<span></span>
  							<span>1.6L</span>
  						</li>
  					</ul>
  				</li>
  			</ul>
  		</div>
  	)
  }
}

class DirectSaleTabUserOrder extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
  	return(
  		<div className="user_order">
  			<p>hold</p>
  		</div>
  	)
  }
}

class DirectSaleDetailTab extends React.Component {
	constructor(props){
		super(props)
	}

	render() {
		Tabs.setUseDefaultStyles(false)
		return (
		  <Tabs
		    onSelect={this.handleSelect}
		    selectedIndex={0} //Tab
		  >
		  
		    <TabList>
		      <Tab></Tab>
		      <Tab></Tab>
		      <Tab></Tab>
		    </TabList>


		    <TabPanel>
		      <DirectSaleTabCarInfo/>
		    </TabPanel>
		    <TabPanel>
		      <DirectSaleTabConf/>
		    </TabPanel>
		    <TabPanel>
		      <DirectSaleTabUserOrder/>
		    </TabPanel>
		  </Tabs>
		)
	}
}

export default DirectSaleDetailTab


