import React from 'react'
import { Link } from 'react-router'
import { observable, computed } from 'mobx'
import { observer } from 'mobx-react'

@observer
class InfoTabComponent extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    let activeClass = ""
    let currentHighlightTabIndex = this.props.infoTabState.currentHighlightTabIndex
    let tabIndex = this.props.tabIndex

    if(currentHighlightTabIndex === tabIndex){
      activeClass = "active"
    }
    return (
      <li className={activeClass}><Link to={'/user_center/123/' + this.props.tabsInfo.link}>{this.props.tabsInfo.text}</Link></li>
    )
  }
}

@observer
class InfoTab extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    const tabsInfoArray = [
      {link: 'my_info', text: ''},
      {link: 'my_subscription', text: ''},
      {link: 'my_favorite', text: ''}
    ]
    return (
      <ul className="info_tab">
        {
          tabsInfoArray.map(
            (value, index) => <InfoTabComponent tabsInfo={value} tabIndex={index} infoTabState={this.props.infoTabState}/>
          )
        }
      </ul>
    )
  }
}

export default InfoTab



