import React from 'react'
import { Link } from 'react-router'
import { observable, computed } from 'mobx'
import { observer } from 'mobx-react'

require('../../sass/user_center/user_base_info.scss')

@observer
class UserBaseInfo extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    return (
      <div className="user_base_info">
        <div className="user_avatar"><img width="100" src="http://static.haomaiche.com/common/images/type/b509656c684942278092e54fd91fb470.png"/></div>
        <div className="contact">
          <p></p>
          <p className="phone_number">186****6565</p>
        </div>
        <div className="logout_btn"><button></button></div>
      </div>
    )
  }
}

export default UserBaseInfo



