import React from 'react'
import { Link } from 'react-router'
import { observable, computed } from 'mobx'
import { observer } from 'mobx-react'

require('../../sass/index/PopupMenu.scss')

@observer
class PopupMenu extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    if(!this.props.popupMenuState.isPopupMenuVisible) {
      return null
    }
    return (
      <div className="popup_menu">
        <button className="close_btn" onClick={this.props.popupMenuState.hidePopupMenu.bind(this.props.popupMenuState)}></button>
        <ul className="menu_list">
          <li><Link to=""></Link></li>
          <li><Link to=""></Link></li>
          <li><Link to=""></Link></li>
          <li><Link to=""></Link></li>
          <li><Link to="">APP</Link></li>
          <li><a href="">400</a></li>
          <li><Link to=""></Link></li>
        </ul>
        <div className="footer">
          <div className="other_btn"><button></button> | <button></button></div>
          <div className="">
            <p> 2013-2016 </p>
            <p></p>
          </div>
        </div>
      </div>
    )
  }
}

export default PopupMenu



