import React from 'react'
import { Link } from 'react-router'
import { observable, computed } from 'mobx'
import { observer } from 'mobx-react'

require('../../sass/user_center/user_history.scss')

@observer
class UserHistoryComponent extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    return (
      <li>
        <h2 className="history_title">{this.props.historyInfo.title}</h2>
        <p className="history_desc">{this.props.historyInfo.desc}</p>
        <p className="sign_up_time">{this.props.historyInfo.signUpTime}</p>
      </li>
    )
  }
}

@observer
class UserHistory extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    const testData = [
      {title: '', desc: '', signUpTime: new Date().getTime()},
      {title: '', desc: '', signUpTime: new Date().getTime()},
      {title: '', desc: '', signUpTime: new Date().getTime()},
      {title: '', desc: '', signUpTime: new Date().getTime()}
    ]

    return (
      <div className="user_history">
        <ul>
          {
            testData.map(
              (value, index) => <UserHistoryComponent historyInfo={value}/>
            )
          }
        </ul>
      </div>
    )
  }
}

export default UserHistory



