import React from 'react'

class DirectSalePriceCarType extends React.Component{
	constructor(props){
		super(props)
	}
	
	render(){
		return(
			<div className="ds_car_price_type">
				<div className="discount">
					<img src="/images/car.png"/>
					<div>7.7</div>	
				</div>
				<div className="car_name">
					<p>  S60L</p>
					<p>2014 1.6  2014 1.6 </p>
					<span className="guide_price">20.35</span>
					<span className="landprice">15.35</span>
				</div>
			</div>
		)
	}
}

export default DirectSalePriceCarType


