import React from 'react'
import { Link } from 'react-router'
import { observable, computed } from 'mobx'
import { observer } from 'mobx-react'

import InfoTab from '../components/user_center/info_tab'
import InfoTabState from '../models/InfoTabState'

require('../sass/my_subscription.scss')

class MySubscriptionState {
  @observable mySubscriptionListData = []

}

@observer
class MySubscriptionListComponent extends React.Component {
  constructor(props) {
    super(props)

  }
  render(){
    return (
      <li className="">
        <div className="subscription_title">
          <span>:{this.props.subscriptionData.time}</span><span>:{this.props.subscriptionData.priceLine}</span><span>:{this.props.subscriptionData.createTime}</span>
        </div>
        <div className="subscription_detail clearfix">
          <div className="car_image">
            <img width="100%" src="http://static.haomaiche.com/common/images/type/b509656c684942278092e54fd91fb470.png"/>
          </div>
          <div className="car_desc">
            <p className="car_series_name">BMW 3</p>
            <p className="car_model_name">340i 3.0T </p>
          </div>
          <div className="order_status">
            <p></p>
          </div>
        </div>
      </li>
    )
  }
}

@observer
class MySubscription extends React.Component {
  constructor(props) {
    super(props)

    this.infoTabState = new InfoTabState({currentHighlightTabIndex:1})
    this.MySubscriptionState = new MySubscriptionState()
  }
  componentDidMount(){
    // const Response = await fetch(
    //   '/getHotCarList?cityCode=310000&time=' + new Date().getTime() + '&source=101&pageSize=6',
    //   // http://10.0.0.100:20010/user/member/subscribes?source=101&time=101
    //   {
    //     method:'GET'
    //   }
    // )
    // const ResponseOK = await Response.ok
    // const ResponseJSON = await Response.json()
    //
    // if(ResponseOK && ResponseJSON.status === '1'){
    //   this.recommandCarListState.recommandCarListData = ResponseJSON.data
    // }
    //
  }
  render(){
    const testData = [
      {carName:'BMW 3',carModel:'2017 340i ',time: '3', createTime: '16-04-08', priceLine: '60', state: 0},
      {carName:'BMW 3',carModel:'2017 340i ',time: '3', createTime: '16-04-08', priceLine: '60', state: 1},
      {carName:'BMW 3',carModel:'2017 340i ',time: '3', createTime: '16-04-08', priceLine: '60', state: 2},
      {carName:'BMW 3',carModel:'2017 340i ',time: '3', createTime: '16-04-08', priceLine: '60', state: 3}
    ]
    return (
      <div className="my_subscription">
        <InfoTab infoTabState={this.infoTabState}/>
        <ul className="subscription_list">
          {
            testData.map(
              (value, index) => <MySubscriptionListComponent subscriptionData={value}/>
            )
          }
        </ul>
        <button className="more_subscription_btn"></button>
        <Link className="direct_sale_link" to="">4S</Link>
      </div>
    )
  }
}

export default MySubscription



