import React from 'react'
import { observer } from 'mobx-react'
import Popup from '../common/Popup'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import OrderQuestionList, { OrderQuestionListBtn } from './order_question_list'
import { CarParityButtonOne } from './car_parity_detail_button'
import CarParity from '../common/car_parity'

@observer
class HistoryPriceListItem extends React.Component {
  constructor(props) {
    super(props)
  }

  getStorage() {
    let { item } = this.props
    let storage
    switch (item.respondReasonableType) {
      case 0:
        storage = ''
        break;
      case 1:
        storage = ''
        break;
      case 2:
        storage = ''
        break;
    }
    return storage
  }

  getTip() {
    let { item } = this.props
    let tip
    switch (item.respondReasonableType) {
      case 1:
        tip = ''
        break;
      case 2:
        tip = ',3'
        break;
      case 3:
        tip = ''
        break;
    }
    return tip
  }

  render() {
    let { item } = this.props
    let tip = this.getTip()
    let storage = this.getStorage()
    return (
      <ul>
        <li>
          <div className="history_price_header">
            <div className="store_parity_user_img">
              <span className="img_wrapper">
                <img src={ item.empPic } />
              </span>
            </div>
            <p>
              <strong>{ item.fsAbbrname }</strong>
              <span>{ item.empName } { item.empPost }</span>
              <span>{ item.respondTime }</span>
            </p>
          </div>
          <div className="history_price_content">
            <div className="content_item">
              <p>
                <span></span>
                <strong><em>{ item.webRespondSource.sourceSum }</em></strong>
              </p>
            </div>
            <div className="content_item">
              <p>
                <span></span>
                <strong><strike>{ item.askpModelPrice }</strike></strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.askpModelPrice }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourcePrice }</strong>
              </p>
            </div>
            <div className="content_item">
              <p>
                <b>{ tip }</b>
              </p>
              <p>
                <span></span>
                <strong>{ storage }</strong>
              </p>
            </div>
            <div className="content_item extra_info">
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourceTax }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourceInsure }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourceLoanCharge }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourceLoan }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourceReplace }</strong>
              </p>
            </div>
            <div className="content_item extra_info">
              <p>
                <span></span>
                <strong>{ item.webRespondSource.sourceBindPrice }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.sourceBind }</strong>
              </p>
              <p>
                <span></span>
                <strong>{ item.packOptionname }</strong>
              </p>
            </div>
            <div className="content_item extra_info last_item">
              <p>
                <span></span>
                <strong>{ item.descContent }</strong>
              </p>
              <p className="over_time">
                <i></i><br/>
                <span> { item.validDate } </span><br/>
                <Popup component={OrderQuestionList} button={OrderQuestionListBtn} popupClassName="pop_bottom" type="bottom" />
              </p>
            </div>
          </div>
          <Popup component={CarParity} button={CarParityButtonOne} data={this.props.carParityState} popupClassName="popup_car_parity" type="center"/>
        </li>
      </ul>
    )
  }
}

@observer
class CarParityDetailHistoryPriceList extends React.Component {
  constructor(props) {
    super(props)
  }

  getHistoryPriceListLabel() {
    let { carParityHistoryPrice } = this.props
    return (
      carParityHistoryPrice.map((historyPrice, i) => {
        return <Tab>{historyPrice.fsAbbrname}</Tab>
      })
    )
  }

  getHistoryPriceListData() {
    let { carParityHistoryPrice } = this.props
    return (
      carParityHistoryPrice.map((historyPrice, i) => {
        return <TabPanel><HistoryPriceListItem item={historyPrice} {...this.props}/></TabPanel>
      })
    )
  }

  render() {
  	return(
  		<div className="history_price_list">
        <Tabs
          onSelect={this.handleSelect}
          selectedIndex={0} //Tab
        >
          <TabList>
            {this.getHistoryPriceListLabel()}
          </TabList>
          {this.getHistoryPriceListData()}
        </Tabs>
  		</div>
  	)
  }
}

export default CarParityDetailHistoryPriceList



