import React, { Component, PropTypes } from 'react'
import Modal from 'react-modal'

class Popup extends Component {
	constructor(props) {
		super(props)

		this.state = {
			modalIsOpen: false
		}

		this.openModal = this.openModal.bind(this)
    this.closeModal = this.closeModal.bind(this)
	}

	openModal() {
		this.setState({
			modalIsOpen: true,
		})
	}

  closeModal() {
		this.setState({
			modalIsOpen: false,
		})
	}

	render() {
    const Children = this.props.component
    const PopupButton = this.props.button
    let customStyles = ''

    switch(this.props.type) {
      case 'center':
        customStyles = customStylesCenter
        break;
      case 'left':
        customStyles = customStylesLeft
        break;
      case 'right':
        customStyles = customStylesRight
        break;
      case 'bottom':
        customStyles = customStylesBottom
        break;
  }

		return (
			<div className="popup">
        <PopupButton openModal={this.openModal} {...this.props} model={this.props.model} />
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.props.afterOpenFn}
          onRequestClose={this.props.onRequestClose}
          shouldCloseOnOverlayClick={true}
          style={customStyles}
          className={this.props.popupClassName}
          overlayClassName={this.props.overlayClassName}
        >
          <Children closeModal={this.closeModal} {...this.props} />
        </Modal>
			</div>
		)
	}
}

Popup.displayName = 'Popup'

const customStylesCenter = {
  overlay : {
    position          : 'fixed',
    top               : 0,
    left              : 0,
    right             : 0,
    bottom            : 0,
    backgroundColor   : 'rgba(0, 0, 0, 0.75)'
  },
  content : {
    position                   : 'absolute',
    top                        : '50px',
    left                       : '30px',
    right                      : '30px',
    bottom                     : '50px',
    border                     : '1px solid #ccc',
    background                 : '#fff',
    WebkitOverflowScrolling    : 'touch',
    outline                    : 'none',
    padding                    : '20px'
  }
}

const customStylesLeft = {
  overlay : {
    position          : 'fixed',
    top               : 0,
    left              : 0,
    right             : 0,
    bottom            : 0,
    backgroundColor   : 'rgba(0, 0, 0, 0.75)'
  },
  content : {
    position                   : 'absolute',
    top                        : '0px',
    left                       : '40px',
    right                      : '0px',
    bottom                     : '0px',
    border                     : '1px solid #ccc',
    background                 : '#fff',
    overflow                   : 'auto',
    WebkitOverflowScrolling    : 'touch',
    outline                    : 'none',
    padding                    : '0'
  }
}

const customStylesRight = {
  overlay : {
    position          : 'fixed',
    top               : 0,
    left              : 0,
    right             : 0,
    bottom            : 0,
    backgroundColor   : 'rgba(0, 0, 0, 0.75)'
  },
  content : {
    position                   : 'absolute',
    top                        : '0px',
    left                       : '0px',
    right                      : '40px',
    bottom                     : '0px',
    border                     : '1px solid #ccc',
    background                 : '#fff',
    overflow                   : 'auto',
    WebkitOverflowScrolling    : 'touch',
    outline                    : 'none',
    padding                    : '20px'
  }
}

const customStylesBottom = {
  overlay : {
    position          : 'fixed',
    top               : 0,
    left              : 0,
    right             : 0,
    bottom            : 0,
    backgroundColor   : 'rgba(0, 0, 0, 0.75)'
  },
  content : {
    position                   : 'absolute',
    top                        : '30px',
    left                       : '0px',
    right                      : '0px',
    bottom                     : '0px',
    background                 : '#fff',
    overflow                   : 'auto',
    WebkitOverflowScrolling    : 'touch',
    outline                    : 'none',
    padding                    : '0'
  }
}

export default Popup



