import React from 'react'

class DirectSaleDetailImgSlide extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<div className="ds_img_slide">
			<img src="/images/direct_sale_detail/car.png"/> 	
		</div>
	)  
  }
}

export default DirectSaleDetailImgSlide


