import React from 'react'

import Banner from '../../components/common/banner'
import DirectCarScroll from '../../components/common/direct_car_scroll'
import Popup from '../../components/common/Popup'
import ChooseDirectSale ,{DirectSaleDetailCarType} from '../../components/direct_sale_detail/choose_direct_detail_car_type'

class DirectSaleDetailLandPrice extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<div className="landprice">
			<div className="car_price">
				<span>:</span>
				<span>230.50</span>
			</div>
			<div className="save">
				<span></span>
				<span>
					<i>232.99</i>
					<i>2.00</i>
				</span>
			</div>
		</div>
	)  
  }
}


class DirectSaleDetailPopup extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<Popup component={ChooseDirectSale} button={DirectSaleDetailCarType} popupClassName="pop_left" type="left" />
	)  
  }
}



class DirectSaleDetailGift extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<div className="gift">
			<img src="/images/direct_sale_detail/gift.png"/>
			<p>2888Limooking12in1</p>
		</div>
	)  
  }
}

class DirectSaleDetailInsurance extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<div className="insurance">
				<div className="title"></div>
				<img src="/images/direct_sale_detail/insurance.png"/>
				<div className="moreQA"></div>
		</div>
	)  
  }
}

class DirectSaleDetailServiceFlow extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<div className="service_flow">
				<div className="title"></div>
		</div>
	)  
  }
}

class DirectSaleDetailTabCarTypeContent extends React.Component {
  constructor(props){
    super(props)
  }
  render(){
	return(
		<div>
			<DirectSaleDetailLandPrice/>
  			<DirectSaleDetailPopup/>
  			<DirectSaleDetailGift/>
  			<DirectSaleDetailInsurance/>
  			<DirectSaleDetailServiceFlow/>
  			<DirectCarScroll/>
        <Banner/>
		</div>
	)  
  }
}

export default DirectSaleDetailTabCarTypeContent


