import React from 'react'
import { observable, computed } from 'mobx'
import { observer } from 'mobx-react'

require('../../sass/index/main_banner.scss')

@observer
class MainBanner extends React.Component {
  constructor(props) {
    super(props)

  }
  showPopupMenu(){
    this.props.popupMenuState.showPopupMenu()
  }
  render(){
    return (
      <div className="main_banner">
        <div className="top_bar clearfix">
          <h1 className="logo"><img width="35" src="/images/index/logo.png"/></h1>
          <button className="menu_btn fr" onClick={this.showPopupMenu.bind(this)}></button>
          <button className="search_btn fr" onClick={this.props.popupSearchState.showPopupSearch.bind(this.props.popupSearchState)}></button>
        </div>
        <div className="price_curve"><img width="215" src="/images/index/price_curve.png"/></div>
        <div className="titles">
          <h2 className="main_title">60,</h2>
          <h3 className="sub_title">3004S</h3>
        </div>
        <button className="select_car_btn"></button>
        <button className="new_user_btn"></button>
      </div>
    )
  }
}

export default MainBanner



