import React from 'react'
import Slider from 'react-slick'
import { GLOBAL_API_DOMAIN } from '../../config'

const settings = {
  className: 'slider variable-width',
  //dotsClass: 'banner_dots',
  dots: true,
  infinite: true,
  speed: 500,
  autoplay: true,
  autoplaySpeed: 2000,
  slidesToShow: 1,
  slidesToScroll: 1,
  arrows: false,
  swipe: true,
  touchMove: true
}

class Banner extends React.Component {
  constructor(props) {
    super(props)

    this.cityCode = '310000'
		this.source = '101'
		this.time = new Date().getTime()
		this.search = '?' + 'source=' + this.source + '&time=' + this.time
		this.options = {
			headers: {'Content-Type': 'application/json'},
			method: 'GET'
		}

    this.state = {
      bannersItem: null
    }
  }

  async componentDidMount() {
    await this.getBanner()
  }

  async getBanner() {
		let url = GLOBAL_API_DOMAIN + '/advert/' + this.cityCode + '/0/1' + this.search
    const Response = await fetch(url, this.options)
    const ResponseOK = await Response.ok
    const ResponseJSON = await Response.json()
    if (ResponseOK) {
      let banners = ResponseJSON.data
      let bannersItem = banners.map((banner, i) => {
        return (
          <div>
            <a href={banner.advertLink}>
              <img width="100%" src={banner.advertPic}/>
            </a>
          </div>
        )
      })
			this.setState({
				bannersItem: bannersItem
			})
    }
  }

  render() {
    if (this.state.bannersItem) {
      return (
  			<div className="banner">
          <Slider {...settings}>
            { this.state.bannersItem }
          </Slider>
  			</div>
  		)
    } else {
      return null
    }
  }
}

export default Banner



